@media only screen and (max-width: 767px) {
  .desktop-only {
    display: none !important;
  }
}

@media only screen and (max-width: 767px) {
  .device-visible {
    display: table-cell !important;
  }
}

/* Uploads Styles */
.open-upload-new {
  background: #FFFFFF !important;
}

.open-upload-new.disabled {
  cursor: not-allowed !important;
}

.actions-upload {
  width: 283px !important;
}

@media only screen and (max-width: 767px) {
  .actions-upload {
    width: 116px !important;
  }
}

.actions-deleted {
  width: 181px !important;
}

@media only screen and (max-width: 767px) {
  .actions-deleted {
    width: 78px !important;
  }
}

td .upload-aligned-text {
  margin: 6px 0 0 10px;
  display: inline-block;
  float: left;
}

@media only screen and (max-width: 767px) {
  td .upload-aligned-text {
    float: none;
    margin: 0;
  }
}

td .upload-aligned-image {
  float: left;
}

@media only screen and (max-width: 767px) {
  td .upload-aligned-image {
    display: none;
  }
}

/* Menu Styles */
.menu-types {
  display: none;
  float: left;
  width: 100%;
  margin-top: 10px;
}

/* Block Styles */
#block-image {
  display: none;
  float: right;
  max-width: 100%;
}

.loading-blocks {
  display: none;
  text-align: center;
}

.block-location-title {
  font-size: 12px;
  font-size: 0.75rem;
  display: block;
  float: left;
  width: 100%;
  padding: 0 0 2px;
  margin: 50px 0 15px 0;
  border-bottom: 1px solid #4AAEE3;
  font-family: 'Open Sans Bold', sans-serif;
  color: #4AAEE3;
  text-transform: uppercase;
}

.block-location-title:first-of-type {
  margin: 0 0 15px 0;
}

.block-item {
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  display: block;
  float: left;
  width: 100%;
  background: #FFFFFF;
  padding: 15px;
  margin: 60px 0 0 0;
  border: 3px dashed #4AAEE3;
}

.block-item-btn {
  width: 33.3%;
  background: #FFF !important;
}

.actions-blocks {
  width: 165px;
  padding: 10px;
}

@media only screen and (max-width: 767px) {
  .actions-blocks {
    display: table-cell;
    width: 78px;
  }
}

.block-assign-container {
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  display: block;
  float: left;
  width: 100%;
  padding: 15px;
  background: #FCFCFC;
  border: 1px solid #EAEAEA;
  border-top: none;
}

.block-assign-select-container {
  width: -moz-calc(100% - 95px);
  width: -webkit-calc(100% - 95px);
  width: calc(100% - 95px);
  float: left;
}

@media only screen and (max-width: 767px) {
  .block-assign-select-container {
    width: -moz-calc(100% - 45px);
    width: -webkit-calc(100% - 45px);
    width: calc(100% - 45px);
  }
}

.block-assign-btn-container {
  width: 95px;
  float: right;
}

@media only screen and (max-width: 767px) {
  .block-assign-btn-container {
    width: 45px;
  }
}

.block-assign-select {
  float: left;
  width: 100%;
  border: 1px solid #EAEAEA;
  padding: 4px 5px 9px;
  max-height: none;
  margin-top: 0;
}

@media only screen and (max-width: 767px) {
  .block-assign-select {
    padding: 4px 5px 8px;
  }
}

.block-inheritance {
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  width: -moz-calc(100% - 2px);
  width: -webkit-calc(100% - 2px);
  width: calc(100% - 2px);
  display: block;
  float: left;
  margin: 5px 0;
  padding: 15px;
  border: 1px solid #FFC65D;
  background: #fffcf6;
}

.block-inheritance span {
  font-size: 13px;
  font-size: 0.8125rem;
  font-family: 'Open Sans', sans-serif;
  color: #7E8890;
}

.block-inheritance em {
  font-size: 13px;
  font-size: 0.8125rem;
  font-family: 'Open Sans Bold', sans-serif;
  color: #7E8890;
}

/* Draft Styles */
#tab-drafts {
  min-height: 52px;
}

.loading-drafts {
  display: none;
  text-align: center;
}

.drafts-table thead > tr > td:first-of-type {
  width: 8px;
  text-align: center;
}

.drafts-table tbody tr > td:first-of-type {
  width: 8px;
  text-align: center;
}

.drafts-table tbody tr.no-drafts > td {
  text-align: left !important;
}

.actions-drafts {
  width: 265px;
  padding: 10px;
}

@media only screen and (max-width: 767px) {
  .actions-drafts {
    display: table-cell;
    width: 117px;
  }
}

.actions-drafted {
  width: 252px !important;
}

@media only screen and (max-width: 767px) {
  .actions-drafted {
    width: 117px !important;
  }
}

/* Revision Styles */
