}

@media only screen and (max-width: 767px) {
  .actions-revisions {
    display: table-cell;
    width: 117px;
  }
}

/* Activity Logs Styles */
.actions-activity-logs {
  width: 85px !important;
}

@media only screen and (max-width: 767px) {
  .actions-activity-logs {
    width: 60px !important;
  }
}

/* Email Styles */
#email-image {
  display: none;
  float: right;
  max-width: 100%;
}

/* User Styles */
.actions-users {
  width: 283px !important;
}

@media only screen and (max-width: 767px) {
  .actions-users {
    width: 116px !important;
  }
}
