section.list {
  padding: 10px 0 0 0;
  border-bottom: none;
}

section.list.list-small {
  width: 300px;
  margin: 0 0 20px;
  border-right: 1px solid #EAEAEA;
  border-bottom: 1px solid #EAEAEA;
}

@media only screen and (max-width: 1247px) {
  section.list.list-small {
    width: 100%;
    border-right: none;
  }
}

@media only screen and (max-width: 1023px) {
  section.list.list-small {
    width: 300px;
    border-right: 1px solid #EAEAEA;
  }
}

@media only screen and (max-width: 639px) {
  section.list.list-small {
    width: 100%;
    border-right: none;
  }
}

section.list.list-big {
  width: -moz-calc(100% - 322px);
  width: -webkit-calc(100% - 322px);
  width: calc(100% - 322px);
  border-left: 1px solid #EAEAEA;
}

@media only screen and (max-width: 1247px) {
  section.list.list-big {
    width: 100%;
    border-left: none;
  }
}

@media only screen and (max-width: 1023px) {
  section.list.list-big {
    width: -moz-calc(100% - 322px);
    width: -webkit-calc(100% - 322px);
    width: calc(100% - 322px);
    border-left: 1px solid #EAEAEA;
  }
}

@media only screen and (max-width: 639px) {
  section.list.list-big {
    width: 100%;
    border-left: none;
  }
}

section.list .list-container {
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  font-size: 13px;
  font-size: 0.8125rem;
  padding: 15px;
  font-family: "Open Sans", sans-serif;
  color: #7E8890;
}

section.list table {
  width: 100%;
  color: #7E8890;
}

section.list table thead > tr {
  font-size: 12px;
  font-size: 0.75rem;
  background: #FFFFFF;
  font-family: 'Open Sans Bold', sans-serif;
  text-transform: uppercase;
}

section.list table thead > tr td:not(:last-of-type) {
  cursor: pointer;
}

section.list table thead > tr td:not(:last-of-type) i {
  color: #C9CBD1;
}

section.list table tbody > tr {
  font-size: 13px;
  font-size: 0.8125rem;
  font-family: 'Open Sans', sans-serif;
}

section.list table tbody > tr.odd {
  background: #FFFFFF;
}

section.list table tbody > tr.even {
  background: #FCFCFC;
}

section.list table tr > td {
  padding: 10px 15px;
  border-bottom: 1px solid #EAEAEA;
}

@media only screen and (max-width: 767px) {
  section.list table tr > td {
    display: none;
  }
}

@media only screen and (max-width: 767px) {
  section.list table tr > td:first-of-type {
    display: table-cell;
  }
}

section.list table tr > td:last-of-type {
  width: 158px;
  padding: 10px;
}

@media only screen and (max-width: 767px) {
  section.list table tr > td:last-of-type {
    display: table-cell;
    width: 78px;
  }
}

section.list table tr > td a:not(.btn) {
  color: #4AAEE3;
  font-family: 'Open Sans Semibold';
  text-decoration: none;
}
